/* @font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
  } */
.titulos h1.tituloPrincipal {
  margin: 1rem 0;
  padding: 3rem 0;
}
.titulos p.tituloSecundarioFecha {
  margin: 0 0 5rem 0;
  padding: 0;
}
.titulos p.tituloSecundarioFecha span.fechaDia,
.titulos p.tituloSecundarioFecha span.fechaAnio {
  position: relative;
}
.titulos p.tituloSecundarioFecha span.fechaDia::before,
.titulos p.tituloSecundarioFecha span.fechaAnio::after {
  content: "";
  position: absolute;
  width: 5rem;
  height: 1px;
  background-color: white;
  margin-top: 1rem;
}
.titulos p.tituloSecundarioFecha span.fechaDia::before {
  left: -6rem;
}
.titulos p.tituloSecundarioFecha span.fechaAnio::after {
  right: -6rem;
}
.titulos p.tituloSecundarioMisQuince {
  font-family: "Clicker Script", cursive;
  font-size: 5rem;
  margin: 0;
  padding: 0;
}

.flux {
  color: rgb(159, 82, 197);
  line-height: 9vw;
  text-shadow: 0 0 3vw rgb(123, 34, 131);
}

.flux {
  animation: flux 2s linear infinite;
  -moz-animation: flux 2s linear infinite;
  -webkit-animation: flux 2s linear infinite;
  -o-animation: flux 2s linear infinite;
}

@keyframes flux {
  0%, 100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 0.4vw #cda5e4, 0.5vw 0.5vw 0.1vw #380747;
    color: white;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #3a0061;
    color: rgb(159, 82, 197);
  }
}
.neon-rojo {
  text-shadow: 0 0 3vw #F40A35;
  animation: neon-rojo 2s infinite alternate;
}

.neon-violeta {
  text-shadow: 0 0 3vw #F40A35;
  animation: neon-violeta 2s infinite alternate;
}

@keyframes neon-rojo {
  0%, 10%, 15%, 20%, 30%, 35%, 40%, 50%, 55%, 60%, 70%, 75%, 80%, 85%, 90%, 100% {
    text-shadow: 0 -1px 4px white, 0 -2px 10px #FB4264, 0 -10px 20px #F40A35;
  }
  10%, 15%, 60% {
    text-shadow: none;
  }
}
@keyframes neon-violeta {
  0%, 10%, 15%, 20%, 30%, 35%, 40%, 50%, 55%, 60%, 70%, 75%, 80%, 85%, 90%, 100% {
    text-shadow: 9px 9px 18px rgb(130, 52, 255), 3px 3px 5px rgb(50, 0, 108), -3px -3px 5px rgb(50, 0, 108), -9px -9px 27px rgb(130, 52, 255);
  }
  10%, 15%, 60% {
    text-shadow: none;
  }
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-snap-type: y mandatory;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: black;
  text-align: center;
  color: white;
}
body.fijarBody {
  overflow: hidden;
}

p, form {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  margin: 0;
}

p:nth-child(1) {
  padding-top: 1.8rem;
}

h1 {
  font-family: "Clicker Script", cursive;
  font-size: 11rem;
  font-weight: 100;
  margin: 0;
  padding: 0;
}

h2 {
  font-family: "Monoton", cursive;
  font-size: 2.4rem;
  font-weight: 200;
  margin: 0;
  padding: 4rem 0 0 0;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 2.2rem;
  font-weight: bold;
  margin: 0;
  padding: 3.5rem 0 1rem 0;
}

h4 {
  font-family: "Clicker Script", cursive;
  font-size: 2.6rem;
  font-weight: 500;
}

img {
  width: 100%;
  height: auto;
}

.textoSombreado {
  text-shadow: -1px -1px 0px rgba(51, 15, 15, 0.4), 2px 2px 0px rgba(51, 15, 15, 0.4), 4px 4px 0px rgba(253, 123, 123, 0.3333333333);
}

/** Header **/
.webp .imagenFiesta {
  background-image: url("../img/letsparty.webp");
}

.no-webp .imagenFiesta {
  background-image: url("../img/letsparty.png");
}

.imagenFiesta {
  background-repeat: no-repeat;
  background-size: 100%;
  padding-bottom: 10rem;
}
.imagenFiesta .iconos {
  width: 6rem;
  margin-top: 18%;
}
@media (min-width: 1025px) {
  .imagenFiesta .iconos {
    margin-top: 20%;
  }
}
@media (min-width: 1025px) {
  .imagenFiesta .lugaryfecha {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
  }
}

.webp .contenedorBloques {
  background-image: url("../img/abstractwallpaper.webp");
}

.no-webp .contenedorBloques {
  background-image: url("../img/abstractwallpaper.jpeg");
}

.contenedorBloques {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: auto;
}
@media (min-width: 769px) {
  .contenedorBloques {
    background-size: cover;
  }
}
.contenedorBloques .bloque {
  height: fit-content;
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 18rem 0;
}
.contenedorBloques .iconos {
  width: 6rem;
}
.contenedorBloques .dresscode > p {
  padding-top: 1.8rem;
}

.linea {
  margin: 0 auto;
  width: 80%;
  height: 1px;
  background-color: white;
  margin-top: 1.5rem;
}
@media (min-width: 769px) {
  .linea {
    width: 40%;
  }
}

.botonEnlace {
  margin: 2rem auto;
  width: 80%;
  background-color: rgba(51, 15, 15, 0.4);
  border-width: 0.2rem;
  border-style: solid;
  border-radius: 5rem;
  border-color: white;
  padding: 0.5rem;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #FB4264;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (min-width: 769px) {
  .botonEnlace {
    width: 30rem;
  }
}
.botonEnlace svg {
  width: 2.2rem;
  color: #FB4264;
  margin: 0 0 0 0.3rem;
}
.botonEnlace:hover {
  cursor: pointer;
}

.textoCentrado {
  text-align: center;
}

.textoBlanco {
  color: white;
}

.textoResaltado {
  font-weight: 800;
  color: #FB4264;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80%;
}
@media (min-width: 769px) {
  .overlay picture {
    max-width: 40rem;
  }
}
@media (min-width: 1025px) {
  .overlay picture {
    max-width: 60rem;
  }
}

.botonOverlay {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: white;
  color: black;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.botonOverlay:hover {
  cursor: pointer;
}

.contenedorFormulario {
  width: 90%;
  margin: 6rem auto;
}
@media (min-width: 769px) {
  .contenedorFormulario {
    width: 70rem;
  }
}
.contenedorFormulario .formulario {
  border-radius: 10rem;
  width: 100%;
}
.contenedorFormulario .formulario fieldset {
  border-radius: 6rem;
  border-color: #ee9baa;
}

.contenedorCampos .campo {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  column-gap: 1rem;
}
@media (min-width: 769px) {
  .contenedorCampos .campo {
    flex-direction: row;
  }
}
.contenedorCampos .campo label {
  margin-top: 1rem;
  text-align: right;
  color: white;
}
@media (min-width: 769px) {
  .contenedorCampos .campo label {
    width: 22rem;
    text-align: right;
    margin-top: 2.2rem;
  }
}
.contenedorCampos .campo input, .contenedorCampos .campo textarea {
  width: 100%;
  background-color: rgba(18, 1, 36, 0.6);
  border-style: solid;
  border-radius: 0.5rem;
  color: white;
}
@media (min-width: 769px) {
  .contenedorCampos .campo input, .contenedorCampos .campo textarea {
    width: 100%;
    margin-top: 2rem;
  }
}
.contenedorCampos .campo textarea {
  height: 10rem;
}

.pie {
  background: rgb(21, 24, 29);
  background: linear-gradient(90deg, rgb(21, 24, 29) 0%, rgba(37, 10, 51, 0.3841911765) 47%, rgb(25, 1, 1) 100%);
  padding: 1rem;
}
.pie p {
  margin: 0;
  text-align: center;
  color: white;
}
.pie span {
  font-weight: 700;
}

.video {
  margin-top: -10rem;
  position: relative;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}